<h2 id="example">Example <a class="header-anchor" href="#example">&#128279;</a></h2>
<p>Use <code v-pre>v-model</code> to bind selected values, and <code v-pre>options</code> as select options.</p>
<multi-select-example ref="multi-select-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">multi-select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;selected&quot;</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">&quot;options&quot;</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">selected</span>: [],
        <span class="hljs-attr">options</span>: [
          {<span class="hljs-attr">value</span>: <span class="hljs-number">1</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option1&#x27;</span>},
          {<span class="hljs-attr">value</span>: <span class="hljs-number">2</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option2&#x27;</span>},
          {<span class="hljs-attr">value</span>: <span class="hljs-number">3</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option3&#x27;</span>},
          {<span class="hljs-attr">value</span>: <span class="hljs-number">4</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option4&#x27;</span>},
          {<span class="hljs-attr">value</span>: <span class="hljs-number">5</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option5&#x27;</span>}
        ]
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- multi-select-example.vue --&gt;</span>
</code></pre>
<h2 id="multiple-limit">Multiple Limit <a class="header-anchor" href="#multiple-limit">&#128279;</a></h2>
<p>Use <code v-pre>limit</code> to restrict the maximum number of options user can select, no limit when set to <code v-pre>0</code>.</p>
<multi-select-limit ref="multi-select-limit"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">multi-select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">&quot;selected&quot;</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">&quot;options&quot;</span> <span class="hljs-attr">:limit</span>=<span class="hljs-string">&quot;3&quot;</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">selected</span>: [],
        <span class="hljs-attr">options</span>: [
          {<span class="hljs-attr">value</span>: <span class="hljs-number">1</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option1&#x27;</span>},
          {<span class="hljs-attr">value</span>: <span class="hljs-number">2</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option2&#x27;</span>},
          {<span class="hljs-attr">value</span>: <span class="hljs-number">3</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option3&#x27;</span>},
          {<span class="hljs-attr">value</span>: <span class="hljs-number">4</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option4&#x27;</span>},
          {<span class="hljs-attr">value</span>: <span class="hljs-number">5</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option5&#x27;</span>}
        ]
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- multi-select-limit.vue --&gt;</span>
