      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">selected</span>: [],
        <span class="hljs-attr">options</span>: [
          {<span class="hljs-attr">value</span>: <span class="hljs-number">1</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option1&#x27;</span>},
          {<span class="hljs-attr">value</span>: <span class="hljs-number">2</span>, <span class="hljs-attr">label</span>:<span class="hljs-string">&#x27;Option2&#x27;</span>},
