<h2 id="option-groups">Option groups <a class="header-anchor" href="#option-groups">&#128279;</a></h2>
<p>If you need grouped options, simply add <code v-pre>group</code> (String) as the name to them.</p>
<multi-select-option-groups ref="multi-select-option-groups"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
