<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference">&#128279;</a></h1>
<h2 id="multiselect-1"><a href="https://github.com/uiv-lib/uiv/blob/0.x/src/components/select/MultiSelect.vue">MultiSelect</a> <a class="header-anchor" href="#multiselect-1">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>v-model</code></td>
<td>Array</td>
<td></td>
<td>✔</td>
<td>The selected values.</td>
</tr>
<tr>
<td><code v-pre>options</code></td>
<td>Array</td>
<td></td>
<td>✔</td>
<td>The select options.</td>
</tr>
<tr>
<td><code v-pre>label-key</code></td>
<td>String</td>
<td>label</td>
<td></td>
<td>Identity key name for label.</td>
</tr>
<tr>
<td><code v-pre>value-key</code></td>
<td>String</td>
<td>value</td>
<td></td>
<td>Identity key name for value.</td>
</tr>
<tr>
<td><code v-pre>limit</code></td>
<td>Boolean</td>
<td>0</td>
<td></td>
<td>Maximum number of options user can select, no limit when set to <code v-pre>0</code>.</td>
</tr>
<tr>
<td><code v-pre>size</code></td>
<td>String</td>
<td></td>
<td></td>
<td>Optional sizes, supported: <code v-pre>sm</code> / <code v-pre>lg</code>.</td>
</tr>
<tr>
<td><code v-pre>block</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Apply block level style.</td>
</tr>
<tr>
<td><code v-pre>placeholder</code></td>
<td>String</td>
<td>Select...</td>
<td></td>
<td>The default text displayed when no options are selected.</td>
</tr>
<tr>
<td><code v-pre>split</code></td>
<td>String</td>
<td>,</td>
<td></td>
<td>The options display spliter.</td>
</tr>
<tr>
<td><code v-pre>filterable</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Append filter input before options (default is filter by item value and label, case ignored).</td>
</tr>
<tr>
<td><code v-pre>filter-placeholder</code></td>
<td>String</td>
<td>Search...</td>
<td></td>
<td>The default text displayed in filter input.</td>
</tr>
